
#page {
  padding-top: 2.5rem;
}

.navbar {
  font-size: 1rem;
}

h2.entry-title {
  margin-bottom: 0;
}

div.entry-meta {
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.widget ul {
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}

.widget ul:last-child {
  margin-bottom: 0;
}

.widget ul li {
  padding-left: 1em;
  text-indent: -1em;
}

.widget ul li:before {
  /*content: "+";
  padding-right: 5px;*/
}

#navbar-searchform {
  width: 300px;
}

#navbar-searchform input {
  border: 1px solid #000;
}

#navbar-searchsubmit {
  border-left: none !important;
}

.navbar .dropdown.show > a {
  background: #4422cc;
}

div.entry-meta {
  color: #999999;
  font-size: 0.8rem;
}

div.entry-meta a {
  color: #999999;
  text-decoration: underline;
}

footer.site-footer {
  color: #cccccc;
  font-size: 0.8rem;
}

footer.site-footer a {
  color: #cccccc;
  text-decoration: underline;
}
